<template>
  <v-container>
    <v-toolbar>
      <v-toolbar-title>仪表盘</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
          <v-btn text v-if="$store.state.login.class=='admin'" @click="s_agent">代理管理</v-btn>
          <v-btn text v-if="$store.state.login.class=='agent'" @click="s_password">修改密码</v-btn>
          <v-btn text v-if="$store.state.login.class=='admin'" @click="s_admin_user_status">已答题用户</v-btn>
          <v-btn text @click="s_user">用户管理</v-btn>
      </v-toolbar-items>
      <template v-if="$vuetify.breakpoint.smAndUp">
        <v-btn icon @click="logout">
          <v-icon>exit_to_app</v-icon>
        </v-btn>
      </template>

    </v-toolbar>
     <v-dialog  v-model="repassword.dialog" max-width="500px">
          <v-card>
            <v-card-title>
              <span class="headline">{{ repassword.formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="12">
                    <v-text-field v-model="repassword.passwordA" label="新密码"></v-text-field>
                  </v-col>
                  
                  <v-col cols="12" sm="12">
                    <v-text-field v-model="repassword.passwordB" label="确认新密码"></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">取消</v-btn>
              <v-btn color="blue darken-1" text @click="save">保存</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
  </v-container>
</template>

<script>
export default {
  name: 'Toolbar',
  data: () => ({
    agent:false,
    repassword: {
        dialog: false,
        formTitle:"修改密码",
        passwordA:'',
        passwordB: '',
      },
  }),
  created(){
  },
  methods:{
    logout(){
      this.$cookies.set("qm_jwt_token","",-1)
      this.$cookies.set("qm_jwt_token_class","",-1)
      this.$store.commit('setLogin',{status:false,class:""})
      this.$store.commit('setAgentPhone', '')
      this.$store.commit('setPage', '')
    },
    s_agent(){
      this.$store.commit('setPage',"admin_agent")
    },
    s_password(){
      this.repassword.dialog = true
    },
    s_admin_user_status(){
      this.$store.commit('setPage',"admin_user_status")
    },
    close(){
      this.repassword.dialog = false
    },
    save(){
      
      if(this.repassword.passwordA.length < 6){
        alert("密码不足6位")
        return
      }
      if(this.repassword.passwordA != this.repassword.passwordB){
        alert("两次密码不一致")
        return
      }
      var url = this.$domain + "/api/qm/password?password="+this.repassword.passwordB
      // password 
      this.$put(url)
      .then((res)=>{
      window.console.log(res)
      if(res.code == 1){
        this.repassword.dialog = false
        window.alert("修改成功")
      }else{
          window.alert(res.msg+":"+res.err)
      }
        this.$store.commit('setProgress',false)
      })
      .catch((err)=>{
        window.alert(err)
        this.$store.commit('setProgress',false)
      })
      
    },
    s_user(){
      if(this.$store.state.login.class == "admin"){
        this.$store.commit('setPage',"admin_user")
      }else{
        this.$store.commit('setPage',"agent_user")
      }

    }

  }
};
</script>
